@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

.navbar{
  font-family: 'Poppins', sans-serif;
  color: blue;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px rgb(45, 6, 170);
  --webkit-box-shadow:  0 8px 6px -6px rgb(161, 62, 186);
  border-bottom: solid rgb(111, 32, 237) !important;
}
nav{
  background-color: blue !important;
}
.nav-link{
  font-weight: 300 !important;
}
.active{
  border-bottom: 2px solid black;
}

.navbar-brand{
  font-weight: 700;
  font-family: "roboto",sans-serif;
  letter-spacing: 3px;
}

/* ==============Footer============== */
.footer{
  color:white;
  padding: 20px;
  background: -webkit-linear-gradient(to right, #434343, #000000 );
  background: linear-gradient(to right, #84c4f5, #8a52e3ab );
}
.footer a{
  text-decoration: none;
  color: white;
  padding: 12px;
}
.footer a:hover{
  color:#ffefba;
  border-bottom: 1px solid #ffefba;
}

/* =============Page Not Found============= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

/* =============contact us============= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* =============register============= */
.register{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}

/* ===============Product Link============ */
.product-link{
  text-decoration: none !important;
  color: black !important;
}

.nav{
  background-color: green;
}

.card{
  background-color: aqua;
}
.card-img-top:hover{
  transform: scale(0.9);
}