.banner-img {
    margin-top: 60px;
  }

.home-page h1 {
    color:chocolate;
    font-family: "Playfair Display", serif;
  }
  .home-page {
  display: flex;
  flex-wrap: wrap;
} 

.home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
  }

  .home-page .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .home-page .card-price {
    color: green;
    font-weight: bold;
  }
  .home-page button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }

  .home-page .card-text {
    color: rgb(78, 73, 73);
  }
  .home-page .card img:hover {
    transform: scale(0.9);
  }

  .home-page .card-img-top {
    height: 300px !important;
  }
  
  /* .............filters............. */

  .filters h4 {
    margin-top: 100px;
    color: rgb(236, 104, 33) !important;
    border-bottom: 1px solid rgba(128, 128, 128, 0.337);
  }

  .ant-checkbox-wrapper:first-of-type {
    margin-left: 8px;
  }

  .ant-checkbox-wrapper {
    margin: 2px;
  }

  .ant-radio-wrapper {
    margin-bottom: 5px !important;
    margin-left: 10px;
  }

  .filters button {
    background-color: rgb(30, 30, 238);
    border: none;
    border-radius: 0;
    margin-top: 20px;
  }

  .loadmore {
    color: blue;
    font-weight: bold;
    font-size: 20px !important;
  }
  